@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  background-color: #e9eaed;
}
.App {
  width: 85%;
  margin: 5px auto;
  /*text-align: center;*/
}

.header {
  display: flex;
  justify-content: space-between;
  margin: 10px 2px;
}
.headerMenu a {
  display: inline-block;
  margin: 0px 10px;
  text-decoration: none;
  color: #565673;
  font-size: 18px;
  letter-spacing: 1.2px;
}
.headerMenu a:hover {
  color: #5f9ea0;
  transform: scale(1.05);
}
.search img {
  padding-left: 0.3cm;
}

.logo {
  font-family: "Dancing Script", cursive;
  font-size: 130px;
  text-align: center;
  background-image: url("/authorwrites-public/static/media/Wood Background.fcb3809b.jfif");
  background-color: white;
  background-repeat: none;
  background-size: contain;
  justify-content: center;
  line-height: inherit;
  min-height: 15cm;
  margin: 10px 10px;
  padding: 40px 42px;
}
.logo p {
  display: inline-block;
  padding-top: 4cm;
}

@media only screen and (max-width: 600px) {
  .logo p {
    font-size: 80px;
    background-repeat: none;
  }
}

.card {
  text-align: center;
}

.card {
  text-align: center;
  margin: 20px 5px;
  min-height: 4cm;
  border: black 2px solid;
}
.btn {
  justify-content: space-around;
}
ul.namingit {
  list-style-type: decimal;
  text-align: left;
  padding-left: 10%;
  padding-top: 10px;
}
.badge {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 18px;
}

.lister {
  border-radius: 5px solid black;
  width: 50%;
}

body {
  background-color: #e9eaed;
}

